import React from 'react';

const Card= (props) =>{
	// const {name,email,id}=props;
	return(
		<div className='tc bg-light-green dib br2 pa1 ma2 grow bw2 shadow-5 t'>
		<img alt='robots' src={`https://robohash.org/${props.id}?100X100`}/>
		<div>
		<h2>{props.name}</h2>
		<p>{props.email}</p>
		</div>
		</div>
		);
}

export default Card;
